//vars
$green: #6dca14;
$red: #e86278;
$white: #fff;
$black: #000;
$gray: #818181;
$gray2: #bdbdbd;
$bg_gray: #f3f3f3;
//fonts

/*=== import ===*/
@include font("Clear Sans Bold Italic", "ClearSans-Bolditalic");
@include font("Clear Sans Bold", "ClearSans-Bold");
@include font("Clear Sans Medium Italic", "ClearSans-Mediumitalic");
@include font("Clear Sans Medium", "ClearSans-Medium");
@include font("Clear Sans Regular", "ClearSans-Regular");
@include font("Clear Sans Italic", "ClearSans-Italic");
@include font("Clear Sans Thin", "ClearSans-Thin");
@include font("Clear Sans Light", "ClearSans-Light");

/*=== mixins ===*/
@mixin bold-italic {
	font-family: "Clear Sans Bold Italic", sans-serif;
	font-weight: normal; }
@mixin bold {
	font-family: "Clear Sans Bold", sans-serif;
	font-weight: normal; }
@mixin medium-italic {
	font-family: "Clear Sans Medium Italic", sans-serif;
	font-weight: normal; }
@mixin medium {
	font-family: "Clear Sans Medium", sans-serif;
	font-weight: normal; }
@mixin regular {
	font-family: "Clear Sans Regular", sans-serif;
	font-weight: normal; }
@mixin regular-italic {
	font-family: "Clear Sans Italic", sans-serif;
	font-weight: normal; }
@mixin thin {
	font-family: "Clear Sans Thin", sans-serif;
	font-weight: normal; }
@mixin light {
	font-family: "Clear Sans Light", sans-serif;
	font-weight: normal; }

// titles
.title-primary {
	text-align: center;
	@include bold;
	font-size: 18px;
	text-transform: uppercase;
	letter-spacing: 3.6px;
	line-height: 1.2;
	color: $black;
	width: 100%;
	span {
		position: relative;
		display: inline-block;
		&:before {
			content: '';
			display: block;
			position: absolute;
			right: 100%;
			top: 50%;
			margin: -1px 20px 0 0;
			width: 75px;
			height: 2px;
			background: $black; } }
	&__left {
		text-align: right;
		padding: 0 588px 0 0px; } }
.title-secondary {
	@include bold;
	font-size: 18px;
	text-align: center;
	letter-spacing: 3.6px;
	text-transform: uppercase;
	color: $black;
	@include xs {
		font-size: 16px; } }
//icons
.icon-arr {
	font-size: 6px;
	fill: $black; }
.icon-iblock-1 {
	font-size: 40px;
	fill: $red; }
.icon-arrd {
	font-size: 24px;
	fill: #bcbcbc;
	@include xs {
		font-size: 16px; } }
.icon-cancel {
	font-size: 16px;
	@include tr(opacity .3s ease); }

//basic styles
body {
	background: $white;
	@include css-lock(16, 50, 600, 1000);
	line-height: 1.4;
	@include regular;
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	// overflow-x: hidden
	width: 100%;
	left: 0;
	position: static;
	top: 0;
	&.is-fixed {
		position: fixed; }
	&.is-overflow {
		overflow: hidden;
		height: 100%; } }

a {
	text-decoration: none;
	color: inherit; }
button {
	background: none;
	outline: none;
	border: 0; }
img {
	max-width: 100%;
	max-height: 100%; }
//buttons
.btn {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	height: 50px;
	padding: 5px 15px;
	position: relative;
	min-width: 200px;
	color: $white;
	font-weight: 400;
	font-size: 14px;
	line-height: 1.4;
	@include bold;
	text-transform: uppercase;
	letter-spacing: 1.4px;
	background: $red;
	transition: background 0.25s;
	&:hover {
		background: darken($red, 5%); }
	&:active {
		top: 1px; }
	&_primary {
		background: $green;
		&:hover {
			background: darken($green, 5%); } }
	&_secondary {
		background: $white;
		font-size: 12px;
		color: $black;
		&:hover {
			background: darken($white, 5%); } } }
.btn-close {
	background-color: $gray2;
	border-radius: 50%;
	width: 31px;
	height: 31px;
	display: flex;
	align-items: center;
	justify-content: center;
	&:hover {
		.icon-cancel {
			opacity: 0.7; } } }
// inputs
%inputs {
	width: 100%;
	outline: 0;
	display: block;
	border: 0; }
.input-field {
	position: relative;
	&__el {
		height: 32px;
		border-radius: 3px;
		background: #f3f3f3;
		padding: 4px 10px;
		font-size: 14px;
		@include regular;
		color: $gray;
		transition: background 0.25s;
		@extend %inputs;
		@include placeholder {
			color: $gray; }
		&:focus {
			background: darken(#f3f3f3, 2%); } } }
//textarea
.input-area {
	position: relative;
	width: 100%;
	&__el {
		resize: none;
		height: 66px;
		background: #f3f3f3;
		padding: 8px 10px;
		font-size: 14px;
		@include regular;
		color: $gray;
		transition: background 0.25s;
		@extend %inputs;
		@include placeholder {
			color: $gray; }
		&:focus {
			background: darken(#f3f3f3, 2%); } } }
// checkbox
.checkbox {
	display: inline-flex;
	position: relative;
	&__list {
		display: flex;
		flex-direction: column;
		padding-left: 5px; }
	&__item {
		position: relative;
		display: flex;
		&:not(:last-child) {
			margin-bottom: 10px; } }
	&__field {
		position: absolute;
		z-index: -1;
		opacity: 0;
		height: 0;
		&:checked + label:after {
			content: "";
			z-index: 3;
			top: 8px;
			left: 6px;
			position: absolute;
			transform: translate(-50%,-50%) rotate(220deg);
			width: 8px;
			height: 8px;
			background: 0 0;
			border-top: 2px solid #000;
			border-left: 2px solid #000; } }
	&__label {
		display: flex;
		align-items: center;
		cursor: pointer;
		position: relative;
		&:before {
			content: "";
			user-select: none;
			overflow: hidden;
			box-shadow: none;
			background: 0 0;
			width: 13px;
			height: 13px;
			border-radius: 3px;
			border: 1px solid #e4e4e4;
			background-color: #f3f3f3;
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			margin: auto; } }
	&__title {
		font-size: 14px;
		letter-spacing: 0.35px;
		padding-left: 26px;
		color: $black; }
	&_right {
		.checkbox__title {
			padding: 0 26px 0 0; }
		.checkbox__label {
			&:before {
				left: auto;
				right: 0; } }
		.checkbox__field {
			&:checked + label:after {
				left: auto;
				right: -2px; } } } }
// range-slider
.range-slider {
	padding-left: 10px;
	width: 90%;
	margin: 33px 0 53px 0;
	&__title,
	&__inputs {
		font-size: 14px;
		color: $gray;
		letter-spacing: 0.35px; }
	&__controls {
		display: flex;
		justify-content: space-between;
		width: 100%;
		margin: 0 -14px	15px; }
	&__col {
		padding: 0 14px; }
	&__input {
		width: 100%;
		display: block;
		min-width: 100px;
		color: $gray;
		padding: 7px 11px;
		border-radius: 3px;
		background: #f3f3f3;
		transition: background 0.25s;
		@extend %inputs;
		@include placeholder {
			color: $gray; }
		&:focus {
			background: darken(#f3f3f3, 2%); } } }
// checkbox

// pagination
.catalog-page {
	display: flex;
	font-size: 14px;
	text-transform: uppercase;
	justify-content: space-between;
	@include regular;
	&__navigation {
		display: flex;
		justify-content: center;
		align-items: center;
		&:last-of-type {
			.catalog-page__first,
			.catalog-page__link {
				border-left-width: 1px;
				border-left-style: solid;
				border-color: $black; }
			.catalog-page__first {
				border-left: none; } } }
	&__title {
		letter-spacing: 0.7px; }
	&__current {
		color: $gray2; }
	&__link {
		color: $black;
		@include tr(color .25s ease);
		line-height: 13px;
		&:hover {
			color: $gray2; } }
	&__first,
	&__link {
		text-decoration: none;
		padding: 0 8px; } }
// catalog-list
.catalog-list {
	display: flex;
	justify-content: center;
	align-items: center;
	&__item {
		padding: 0 15px;
		flex: 1;
		display: flex; }
	&__link {
		color: $black;
		font-size: 14px;
		text-transform: uppercase;
		letter-spacing: 1.4px;
		border-bottom: 2px solid $black;
		@include bold;
		transition: color 0.25s, border-color 0.25s;
		padding: 10px 45px;
		&:hover {
			color: lighten($black, 50%);
			border-color: lighten($black, 50%); }
		&.is-active {
			color: $red;
			border-color: $red; } } }
// select
.select-l {
	position: relative;
	display: flex;
	&:after {
		content: '';
		display: block;
		position: absolute;
		z-index: 1;
		top: 0;
		right: 10px;
		bottom: 0;
		margin: auto;
		pointer-events: none;
		@include arr(9, 4, $black, b); }
	&__el {
		cursor: pointer;
		border-radius: 3px;
		background-color: #f3f3f3;
		letter-spacing: 0.35px;
		font-size: 14px;
		height: 32px;
		padding: 4px 10px;
		color: #0d0113;
		appearance: none;
		border: 0;
		display: block;
		width: 100%;
		@include regular; }
	&_wh {
		width: 208px;
		margin-bottom: 0;
		.select-l__el {
			background-color: $white; } } }

// tags
.tags {
	font-size: 18px;
	@include regular;
	display: flex;
	align-items: center;
	line-height: 30px;
	text-transform: uppercase;
	&__txt {
		margin: 0 5px 0 0; }
	&__item {
		padding: 5px 3px;
		color: #fe3e5d;
		cursor: pointer;
		@include tr(opacity .25s ease);
		&:hover {
			opacity: 0.5; } } }
