

.subjects {
	background-color: $bg_gray;
	&__in {
		padding-top: 38px;
		padding-bottom: 85px; }
	&__promo {
		font-size: 42px;
		padding-left: 16px;
		@include bold; }
	&__tags {
		margin-top: 20px;
		margin-left: 2px; } }
.subjects-l {
	padding-bottom: 42px;
	border-bottom: 1px solid #dedede;
	&__figure {
		text-align: center;
		font-size: 0;
		margin-top: 24px; }
	&__pic {
		height: 480px;
		@include cover;
		width: 100%; }
	&__desc {
		font-size: 12px;
		@include bold;
		margin-top: 2px; }
	&__item {
		margin-top: 40px; }
	&__title {
		color: $black;
		font-size: 24px;
		@include bold; }
	&__title,
	&__content {
		padding-left: 90px;
		max-width: 986px;
		line-height: 30px; }
	&__content {
		font-size: 18px;
		color: $gray;
		margin-top: 26px;
		ul {
			margin-top: 30px; } } }

.hero {
	margin-top: 18px;
	padding: 0 9px;
	&__top {
		padding: 9px 0;
		border-top: 1px solid $gray2;
		border-bottom: 1px solid $gray2;
		display: flex;
		justify-content: space-between;
		align-items: center; }
	&__footer {
		margin-top: 17px;
		font-size: 18px;
		color: $gray;
		line-height: 30px;
		padding-left: 90px;
		max-width: 1010px; } }
// author
.author {
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 10px;
	&__pic {
		width: 70px;
		height: 70px;
		border-radius: 50%;
		position: relative;
		img {
			@include vcenter;
			object-fit: cover;
			border-radius: 50%; } }
	&__col {
		display: flex;
		align-items: center;
		justify-content: center;
		margin-left: 15px; }
	&__date {
		color: $gray;
		margin-left: 5px; } }

.articles-news {
	margin-top: 73px;
	.news__list {
		margin-top: 36px; } }
