.form {
	box-shadow: 0 10px 49px rgba(28, 28, 28, 0.15);
	border-radius: 3px;
	background: $white;
	color: $gray;
	padding: 30px 20px 20px;
	@include xs {
		padding: 15px; }
	&__in {
		display: flex;
		justify-content: center;
		margin-top: 27px;
		font-size: 14px;
		@include md {
			flex-direction: column; }
		@include xs {
			margin-top: 15px; } }
	&__text {
		line-height: 20px;
		&:not(:first-child) {
			margin-top: 24px;
			@include xs {
				margin-top: 15px; } } }
	&__col {
		flex: 1;
		&:last-of-type {
			margin: 4px 0 0 30px;
			@include md {
				margin: 20px 0 0 0; } } }
	&__row {
		margin-bottom: 9px;
		width: 100%; }
	&__field,
	&__textarea {
		border-radius: 3px;
		padding: 9px 13px;
		color: #939393;
		background-color: $bg_gray;
		@extend %inputs;
		font-size: 14px; }
	&__textarea {
		min-height: 69px;
		resize: none;
		letter-spacing: 0.35px; }
	&__footer {
		text-align: center;
		margin-top: 13px; }
	&__btn {
		font-size: 12px;
		letter-spacing: 0.6px; }
	&__select {
		min-width: 78px;
		margin-right: 10px;
		.select-l__el {
			height: 34px; } } }
.form__row-fl {
	display: flex;
	justify-content: flex-start;
	align-items: flex-start; }
