/*=== mixins ===*/
.title-primary {
  text-align: center;
  font-family: "Clear Sans Bold", sans-serif;
  font-weight: normal;
  font-size: 18px;
  text-transform: uppercase;
  letter-spacing: 3.6px;
  line-height: 1.2;
  color: #000;
  width: 100%;
}

.title-primary span {
  position: relative;
  display: inline-block;
}

.title-primary span:before {
  content: '';
  display: block;
  position: absolute;
  right: 100%;
  top: 50%;
  margin: -1px 20px 0 0;
  width: 75px;
  height: 2px;
  background: #000;
}

.title-primary__left {
  text-align: right;
  padding: 0 588px 0 0px;
}

.title-secondary {
  font-family: "Clear Sans Bold", sans-serif;
  font-weight: normal;
  font-size: 18px;
  text-align: center;
  letter-spacing: 3.6px;
  text-transform: uppercase;
  color: #000;
}

@media only screen and (max-width: 599px) {
  .title-secondary {
    font-size: 16px;
  }
}

.icon-arr {
  font-size: 6px;
  fill: #000;
}

.icon-iblock-1 {
  font-size: 40px;
  fill: #e86278;
}

.icon-arrd {
  font-size: 24px;
  fill: #bcbcbc;
}

@media only screen and (max-width: 599px) {
  .icon-arrd {
    font-size: 16px;
  }
}

.icon-cancel {
  font-size: 16px;
  transition: opacity 0.3s ease;
}

body {
  background: #fff;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
  font-family: "Clear Sans Regular", sans-serif;
  font-weight: normal;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  left: 0;
  position: static;
  top: 0;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

body.is-fixed {
  position: fixed;
}

body.is-overflow {
  overflow: hidden;
  height: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  background: none;
  outline: none;
  border: 0;
}

img {
  max-width: 100%;
  max-height: 100%;
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  padding: 5px 15px;
  position: relative;
  min-width: 200px;
  color: #fff;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;
  font-family: "Clear Sans Bold", sans-serif;
  font-weight: normal;
  text-transform: uppercase;
  letter-spacing: 1.4px;
  background: #e86278;
  transition: background 0.25s;
}

.btn:hover {
  background: #e54c65;
}

.btn:active {
  top: 1px;
}

.btn_primary {
  background: #6dca14;
}

.btn_primary:hover {
  background: #60b312;
}

.btn_secondary {
  background: #fff;
  font-size: 12px;
  color: #000;
}

.btn_secondary:hover {
  background: #f2f2f2;
}

.btn-close {
  background-color: #bdbdbd;
  border-radius: 50%;
  width: 31px;
  height: 31px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn-close:hover .icon-cancel {
  opacity: 0.7;
}

.input-field__el, .input-area__el, .range-slider__input, .form__field, .form__textarea, .search__field, .user__input {
  width: 100%;
  outline: 0;
  display: block;
  border: 0;
}

.input-field {
  position: relative;
}

.input-field__el {
  height: 32px;
  border-radius: 3px;
  background: #f3f3f3;
  padding: 4px 10px;
  font-size: 14px;
  font-family: "Clear Sans Regular", sans-serif;
  font-weight: normal;
  color: #818181;
  transition: background 0.25s;
}

.input-field__el::placeholder {
  color: #818181;
}

.input-field__el:focus {
  background: #eeeeee;
}

.input-area {
  position: relative;
  width: 100%;
}

.input-area__el {
  resize: none;
  height: 66px;
  background: #f3f3f3;
  padding: 8px 10px;
  font-size: 14px;
  font-family: "Clear Sans Regular", sans-serif;
  font-weight: normal;
  color: #818181;
  transition: background 0.25s;
}

.input-area__el::placeholder {
  color: #818181;
}

.input-area__el:focus {
  background: #eeeeee;
}

.checkbox {
  display: inline-flex;
  position: relative;
}

.checkbox__list {
  display: flex;
  flex-direction: column;
  padding-left: 5px;
}

.checkbox__item {
  position: relative;
  display: flex;
}

.checkbox__item:not(:last-child) {
  margin-bottom: 10px;
}

.checkbox__field {
  position: absolute;
  z-index: -1;
  opacity: 0;
  height: 0;
}

.checkbox__field:checked + label:after {
  content: "";
  z-index: 3;
  top: 8px;
  left: 6px;
  position: absolute;
  transform: translate(-50%, -50%) rotate(220deg);
  width: 8px;
  height: 8px;
  background: 0 0;
  border-top: 2px solid #000;
  border-left: 2px solid #000;
}

.checkbox__label {
  display: flex;
  align-items: center;
  cursor: pointer;
  position: relative;
}

.checkbox__label:before {
  content: "";
  user-select: none;
  overflow: hidden;
  box-shadow: none;
  background: 0 0;
  width: 13px;
  height: 13px;
  border-radius: 3px;
  border: 1px solid #e4e4e4;
  background-color: #f3f3f3;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}

.checkbox__title {
  font-size: 14px;
  letter-spacing: 0.35px;
  padding-left: 26px;
  color: #000;
}

.checkbox_right .checkbox__title {
  padding: 0 26px 0 0;
}

.checkbox_right .checkbox__label:before {
  left: auto;
  right: 0;
}

.checkbox_right .checkbox__field:checked + label:after {
  left: auto;
  right: -2px;
}

.range-slider {
  padding-left: 10px;
  width: 90%;
  margin: 33px 0 53px 0;
}

.range-slider__title, .range-slider__inputs {
  font-size: 14px;
  color: #818181;
  letter-spacing: 0.35px;
}

.range-slider__controls {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0 -14px	15px;
}

.range-slider__col {
  padding: 0 14px;
}

.range-slider__input {
  width: 100%;
  display: block;
  min-width: 100px;
  color: #818181;
  padding: 7px 11px;
  border-radius: 3px;
  background: #f3f3f3;
  transition: background 0.25s;
}

.range-slider__input::placeholder {
  color: #818181;
}

.range-slider__input:focus {
  background: #eeeeee;
}

.catalog-page {
  display: flex;
  font-size: 14px;
  text-transform: uppercase;
  justify-content: space-between;
  font-family: "Clear Sans Regular", sans-serif;
  font-weight: normal;
}

.catalog-page__navigation {
  display: flex;
  justify-content: center;
  align-items: center;
}

.catalog-page__navigation:last-of-type .catalog-page__first,
.catalog-page__navigation:last-of-type .catalog-page__link {
  border-left-width: 1px;
  border-left-style: solid;
  border-color: #000;
}

.catalog-page__navigation:last-of-type .catalog-page__first {
  border-left: none;
}

.catalog-page__title {
  letter-spacing: 0.7px;
}

.catalog-page__current {
  color: #bdbdbd;
}

.catalog-page__link {
  color: #000;
  transition: color 0.25s ease;
  line-height: 13px;
}

.catalog-page__link:hover {
  color: #bdbdbd;
}

.catalog-page__first, .catalog-page__link {
  text-decoration: none;
  padding: 0 8px;
}

.catalog-list {
  display: flex;
  justify-content: center;
  align-items: center;
}

.catalog-list__item {
  padding: 0 15px;
  flex: 1;
  display: flex;
}

.catalog-list__link {
  color: #000;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 1.4px;
  border-bottom: 2px solid #000;
  font-family: "Clear Sans Bold", sans-serif;
  font-weight: normal;
  transition: color 0.25s, border-color 0.25s;
  padding: 10px 45px;
}

.catalog-list__link:hover {
  color: gray;
  border-color: gray;
}

.catalog-list__link.is-active {
  color: #e86278;
  border-color: #e86278;
}

.select-l {
  position: relative;
  display: flex;
}

.select-l:after {
  content: '';
  display: block;
  position: absolute;
  z-index: 1;
  top: 0;
  right: 10px;
  bottom: 0;
  margin: auto;
  pointer-events: none;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 4px 4.5px 0 4.5px;
  border-color: #000 transparent transparent transparent;
}

.select-l__el {
  cursor: pointer;
  border-radius: 3px;
  background-color: #f3f3f3;
  letter-spacing: 0.35px;
  font-size: 14px;
  height: 32px;
  padding: 4px 10px;
  color: #0d0113;
  appearance: none;
  border: 0;
  display: block;
  width: 100%;
  font-family: "Clear Sans Regular", sans-serif;
  font-weight: normal;
}

.select-l_wh {
  width: 208px;
  margin-bottom: 0;
}

.select-l_wh .select-l__el {
  background-color: #fff;
}

.tags {
  font-size: 18px;
  font-family: "Clear Sans Regular", sans-serif;
  font-weight: normal;
  display: flex;
  align-items: center;
  line-height: 30px;
  text-transform: uppercase;
}

.tags__txt {
  margin: 0 5px 0 0;
}

.tags__item {
  padding: 5px 3px;
  color: #fe3e5d;
  cursor: pointer;
  transition: opacity 0.25s ease;
}

.tags__item:hover {
  opacity: 0.5;
}

.inner {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}

.out {
  flex-grow: 1;
}

/* == Header start == */
.header {
  position: relative;
}

.header__top {
  height: 38px;
  display: flex;
